
.vimeography-thumbnail-container {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, 235px);
  grid-auto-rows: 235px;
  justify-content: center;
  grid-column-gap: 0;
  grid-row-gap: 0;
  margin-bottom: 2rem;
}

/* IE11 Support */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .vimeography-thumbnail-container {
    display: flex;
    flex-wrap: wrap;
  }
}
